
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { getBarInfo, bindBarManager } from '@/api/'
export default {
data () {
return {
info: {
name: '',
phone: '',
barname: 'CMK酒吧'
},
loading: false
}
},
components: {
Group,
XInput,
XButton
},
created () {
getBarInfo({ht_id: this.$route.params.id}).then((res) => {
this.info.barname = res.result.name
})
},
beforeRouteEnter (to, from, next) {
document.title = '酒吧管理绑定'
next()
},
methods: {
bindAction () {
this.$validator.validateAll().then(result => {
let getErrors = this.vErrors.all()
if (getErrors.length > 0) {
this.$vux.toast.show({
text: getErrors[0],
width: '10em'
})
} else {
this.loading = true
bindBarManager({ht_id: this.$route.params.id, name: this.name, phone: this.phone}).then((res) => {
this.$vux.toast.show({
text: '绑定成功',
type: 'success'
})
})
}
})
}
}
}



